body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.search-form {
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.submit-btn {
  width: 100px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 16px;
  transition: background-color .3s linear, color .3s linear;
}

.submit-btn:hover, .submit-btn:focus {
  color: #fff;
  background-color: #0f3443;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery li {
  height: 450px;
  overflow: hidden;
}

.gallery img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.gallery a {
  color: #2a2a2a;
  text-decoration: none;
}

.load-more {
  width: 200px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-top: 50px;
  margin-bottom: 30px;
  margin-left: 50%;
  font-size: 16px;
  transition: background-color .3s linear, color .3s linear;
  transform: translateX(-50%);
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #0f3443;
}

.load-more.is-hidden {
  display: none;
}

.end-collection-text {
  text-align: center;
  margin: 30px 0;
  font-size: 20px;
}

.end-collection-text.is-hidden {
  display: none;
}

